<script>
    import cookie from '../../util/cookie'

    export default {
        name: "login",
        data() {
            return {
                user: '',
                password: '',
                error: false,
                error_msg : '帐户或密码错误',
                userEmpty: false,
                passwordEmpty: false,
            }
        },
        mounted() {
            if (this.isLogin()) {
                this.$message({
                    message: '登录成功，跳转中...',
                    type: 'success'
                });
                setTimeout(() => {
                    this.jump();
                }, 1000)
            }
            ;
        },
        methods: {
            keyup(e) {
                if(e.keyCode !== 13){
                    this.error = false;
                    this.userEmpty = this.user ? false : true;
                    this.passwordEmpty = this.password ? false : true;
                }
            },
            async submit() {
                if (!this.check()) return;

                try {
                    let res = await this.$api.login({
                        username: this.user,
                        pwd: this.password,
                    })

                    if (res.data.code === 200) {
                        cookie.setItem('user', JSON.stringify(res.data.data), '20000', '/');
                        this.$store.commit('updateUser', res.data.data);
                        this.jump();
                    } else {
                        this.error_msg = res.data.msg;
                        this.error = true;
                    }
                } catch (e) {
                    this.$message.error('网络错误，请稍候再试')
                }
            },
            check() {
                if (!this.user || !this.password) {
                    this.error = true;
                    return false;
                }
                return true;
            },

            jump() {
                this.$router.push('/dashboard');
            },

            //检测是否登录
            isLogin() {
                if (cookie.getItem('JSESSIONID')) {
                    return true;
                }
                return false;
            }
        }
    }
</script>


